<template>
    <div class="tuoguan">
        <div v-if="$route.name === '公号托管'">
            <Button class="create" type="success" to="/manage/gonghao/tuoguan/create">接管新公号</Button>
            <Button style="float:right;" type="warning" @click="reset">
                <span style="color: #000;">(慎用)</span>
                重置code
            </Button>
            <Table class="tuoguan-table" stripe :columns="tuoguanHeader" :data="tuoguanData"></Table>
            <Page show-total :total="dataList.length" :page-size="10" @on-change="changePage" />
        </div>
        <router-view />
    </div>
</template>

<script>
export default {
  data() {
    return {
      isCreate: true,
      code: null,
      name: "",
      dataList: [],
      tuoguanData: [],
      tuoguanHeader: [
        {
          key: "code",
          title: "code",
          align: "center",
          width: 80
        },
        {
          key: "name",
          title: "公号名称",
          align: "center"
        },
        {
          key: "appid",
          title: "appid",
          align: "center"
        },
        {
          key: "appsecret",
          title: "appsecret",
          align: "center"
        },
        {
          key: "token",
          title: "token",
          align: "center"
        },
        {
          key: "EncodingAESKey",
          title: "EncodingAESKey",
          align: "center"
        },
        {
          key: "url",
          title: "链接",
          align: "center",
          render: (h, params) => {
            let url = "http://test.oorggt.top/weichat/" + params.row.code;
            return h(
              "span",
              {
                props: {}
              },
              url
            );
          }
        },
        {
          title: "接管状态",
          align: "center",
          render: (h, params) => {
            let status = (params.row.status == -2 ? '未接管' : (params.row.status == -1 ? '接管中' : '已接管'))
            return h(
              "span",
              {
                props: {}
              },
              status
            );
          }
        },
        {
          title: "Action",
          align: "center",
          width: 240,
          render: (h, params) => {
            let that = this;
            return h("div", [
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.$router.push({
                        name: "修改公号",
                        query: params.row
                      });
                    }
                  }
                },
                "编辑"
              ),
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "warning",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.$router.push({
                        name: "配置公号",
                        query: { code: params.row.code }
                      });
                    }
                  }
                },
                "配置"
              ),
              h(
                "Button",
                {
                  props: {
                    type: "error",
                    size: "small"
                  },
                  style: {
                    marginRight: "10px"
                  },
                  on: {
                    click() {
                      that.$Modal.confirm({
                        title: '提示:',
                        content: '确认删除这条内容吗？',
                        onOk: () => {
                          that.$axios.get('/conf/del', {params: { id: params.row._id }})
                          .then(res => {
                            that.tuoguanData.splice(params.index, 1);
                            that.$Message.info(res.data.success);
                          });
                        }
                      })
                    }
                  }
                },
                "删除"
              ),
              h(
                "Button",
                {
                  props: {
                    type: "success",
                    size: "small",
                    disabled: params.row.status == -2 ? false : true
                  },
                  on: {
                    click() {
                      that.$Modal.confirm({
                        title: '提示:',
                        content: '确认开始接管吗？',
                        onOk: () => {
                          that.$axios.get('/conf/jieguan', {params: { code: params.row.code }})
                          .then(res => {
                            that.$Message.info('开始接管');
                            that.$router.go(0)
                          });
                        }
                      })
                      
                    }
                  }
                },
                "接管"
              )
            ]);
          }
        }
      ]
    };
  },
  mounted() {
    this.showDataList();
  },
  methods: {
    showDataList() {
      this.$axios.get("/conf").then(res => {
        this.dataList = res.data.data;
        this.tuoguanData = this.dataList.slice(0,10)
      });
    },
    reset() {
      this.$axios.get("/conf/reset").then(res => {
        this.$Message.info(res.data.success);
      });
    },
    changePage(index){
      var _start = ( index - 1 ) * 10;
      var _end = index * 10;
      this.tuoguanData = this.dataList.slice(_start,_end);
      this.goTop()
    },
    goTop () {
      let self = this
      self.timer = setInterval(function () {
        let osTop = document.documentElement.scrollTop || document.body.scrollTop
        let ispeed = Math.floor(-osTop)
        document.documentElement.scrollTop = document.body.scrollTop = osTop + ispeed
        self.isTop = true
        if (osTop === 0) {
          clearInterval(self.timer)
        }
      }, 30)
    }
  }
};
</script>

<style scoped>
.create {
  margin-bottom: 30px;
  float: left;
}

.tuoguan-table {
  width: 100%;
  clear: both;
  margin-bottom: 30px;
}
</style>
